import { Injectable } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';
import { map, first } from 'rxjs/operators';

const API_URL = 'api/confmanager/models/';

@Injectable({
  providedIn: 'root'
})
export class ModelService {

  constructor(private http: HttpClient) {}

  add(model:{}) {
    return this.http.post<any>(API_URL + 'add/', model)
      .pipe(
        first(),
        map( (data) => {
          return data
        } )
      )
  }

  get(id:number) {
    const params = new HttpParams()
    .set('id', id.toString());
    return this.http.get<any>(API_URL + 'edit/', { params })
      .pipe(
        first(),
        map( (data) => {
          return data.model
        } )
      )
  }

  save(model:{}) {
    return this.http.post<any>(API_URL + 'edit/', model)
      .pipe(
        first(),
        map( (data) => {
          return data
        } )
      )
  }
}
